
.chat__icon
{
    width: 4rem;
    height: 4rem;
    position: fixed;
    z-index: 999;
    bottom: 5rem;
    text-align: center;
    align-items: center;
    background: #ffffff;
    border-radius: 50%;
    right: 5rem;
    cursor: pointer;
    font-size: 3rem;
    color: var(--color-primary);
    box-shadow: 5px 5px 10px #acabab;
}

.chat__icon svg 
{
    margin-top: 0.4rem;
}

.chat__container
{
    width: 20%;
    height: 45%;
    display: block;
    border-radius: var(--border-radius-2);
    overflow: hidden;
    position: fixed;
    z-index: 999;
    bottom: 9.5rem;
    align-items: center;
    background: #ffffff;
    right: 5rem;
    box-shadow: 5px 5px 10px #acabab;
}
.top__infos 
{
    display: flex;
    align-items: center;
    padding: 0.5rem;


    border-bottom: solid #918f8f48;
}
.top__infos .avatar
{
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 50%;
}
.top__infos .info{
    display: block;
    font-family: "Montserrat", sans-serif;
    padding: 0.5rem;
}

.chat__body
{
    padding: 0.8rem;
    display:block;
    position:relative;
    overflow-y: auto;
    max-height: 78%;
}

