.chat__body .sender-message
{
    background-color: var(--color-primary);
    float:right;
    margin-bottom:0.5rem;
    width: 70%;
    font-size:0.8rem;
    padding:1rem;
    color:white;
    text-justify:inner-word;
    border-radius: 1rem 1rem 0 1rem

}

.chat__body .receiver-message
{
    float:left;
    margin-bottom:0.5rem;
    background-color: #cacacaa0;
    width: 70%;
    padding:1rem;
    font-size:0.8rem;
    border-radius: 1rem 1rem 1rem 0
}

.chat__body .receiver-message.thinking
{
    float:left;
    margin-bottom:0.5rem;
    background-color: transparent;
    text-align:start;
    width: 15%;
    padding:1rem;
    font-size:0.8rem;
    border-radius: 1rem 1rem 1rem 0
}



.chat__footer
{
    border-top:1px solid #a8a6a6;
    padding:0.5rem;
    position:absolute;
    bottom:0;
    width:100%;
    display:flex;
    height:4rem;
    /* background:red; */

}

.chat__footer input
{
    width:100%;
    font-size:0.9rem;
    height:auto;
    padding-left:0.5rem;
    border-radius: var(--border-radius-2)
}

.chat__footer .send-btn
{
    cursor:pointer;
    display:flex;
    align-items:center;
    justify-content:center;
    padding:0.7rem;
    width:3rem;
    height:3rem;
    background: var(--color-primary);
    color:white;
    border-radius:50%;
    font-size:1.2rem;
   
}