header {
    margin-top: 5rem;
    height: calc(100vh - 5rem);
    display: grid;
    place-items: center;
    background: var(--color-white);
    transition: (--transition);
}

.header__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}

.header__profile
{
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

 .lottie__profile {

    width: 500px;
    height: auto;
    transform: translate(4%,20%);
    /* box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2); */

}


.shadow_img 
{
    position: absolute;
    width: 100%;
    height: 150px;
    bottom: -1rem;
    background: linear-gradient(to top, rgba(255, 255, 255, 1),
     rgba(255, 255, 255, 0));

}



