.header__container h3 {
    margin: 0.5rem 0;
    z-index: 2;
    font-size: 1.4rem;
}

.header__container h4 {
    font-size: 1rem;
    color: var(--color-dark);
    text-align: center;
}

.header__container p {
    width: 35rem;
    text-align: center;
    margin: 0.6rem 0 2rem;
}

.header__cta {
    display: flex;
    align-items: center;
    gap: 1.2rem;
}

.header__socials {
    position: absolute;
    left: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    gap: 1rem;

}

.header__socials a {
    width: 1.8rem;
    aspect-ratio: 1/1;
    background: var(--color-light);
    color: var(--color-black);
    display: grid;
    place-items: center;
    border-radius: var(--border-radius-3);
}

.header__socials a:hover {
    background: var(--color-primary);
    color: var(--color-white);
    transform: translateX(-0.5rem);

}



/* MEDIA QUERIES (tablets) */
@media screen and (max-width: 1024px) {

    header {
        margin-top: 0;
        height: 100vh;

    }
    .header__profile
    {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
    }
    
     .lottie__profile {
        width: 320px;
        height: auto;
        transform: translateY(19%);
        /* top: 0; */
    }
  
