#contact{
    margin: 0;
    padding: 2rem  10% 6rem !important;
}

.section__title{
    margin-bottom: 1.5rem;
    text-align: center;
}
.contact__container{
    display: grid;
    grid-template-columns: 4fr 8fr;
    padding: 0 5rem;
    column-gap: 1.875rem;
}

.contact__title{
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.contact__details{
    font-size: 1rem;
}

.contact__form-group{
    display: grid;
    grid-template-columns: repeat(2,1fr) ;
    column-gap: 1.5rem;

}

.contact__form-group-div{
    position: relative;
    margin-bottom: 1.875rem;
    height: 3.75rem;

}

.contact__form-input{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    box-shadow: 1px 1px 10px  #3333333f;
    background-color: var(--color-white);
    color: var(--color-black);
    border: none;
    outline: none;
    border-radius: var(--border-radius-1);
    padding: 0.625rem 1.875rem;
    z-index: 1;
}

.contact__form-area {
    height: 10.25rem;
}

.contact__form-area textarea{
resize: none;
}

.contact__btn{
    padding: 0.8rem 4rem;
    text-align: center;
    font-size: 1rem;
    transition: var(--transition);
}


@media screen and (max-width:1024px){

    .contact__container{
       width: 100%;
        padding: 0 1rem;
        
    }
    
    
}
/*MEDIA QUERIES FOR (phones)*/
@media screen and (max-width:600px){

    #contact{
        padding: 0.2rem 0.2rem !important;
    }

    .section__title{
        margin-bottom: 1rem;
        margin-top: 1rem;
        text-align: center;
    }
    .contact__container{
        display: block;
        width: 100%;
         
     }

     .contact__form-group{
        display: block
    
    }

     .contact__title{
        font-size: 1rem;
        margin-bottom: 0.2rem;
    }

    .contact__details{
        margin-bottom: 1rem;
    }
}