.about__container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(40%, 1fr));
  gap: 12%;
}
.about__container p {
  font-weight: 500;
  color: var(--color-black);
}
.about__portrait {
  position: relative;
  width: 30rem;
  height: fit-content;
  max-height: 30rem;
  border-radius: 50%;
  overflow: hidden;
  border: 1rem solid var(--color-white);
  transition: var(--transition);
}
.about__portrait img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.about__left{
  display: flex;
  flex-direction: row;
  justify-content: center;

   
}
.video_cover {
  width: 100%;
  height: 100%;
  position: absolute;
  display: none;
  transition: var(--transition);
}

.video_cover.show {
  display: block;
}
.video_cover video {
  width: 100%;
  height: auto;
  object-fit: cover;
}
.cirlce {
  position: relative;
  display: flex;
  justify-content: center;
}
.video__btn {
  position: absolute;
  z-index: 2;
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  background: var(--color-primary);
  top: -5%;
  
}

.video__btn button {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 1.2rem;
  background-color: transparent;
  z-index: 2;
  cursor: pointer;
  color: var(--color-white);
}
@keyframes shadow {
  0% {
    box-shadow: none;
  }
  50% {
    box-shadow: 0 0 10px 5px var(--color-hover);
  }
  100% {
    box-shadow: none;
  }
}

.video__btn.shake-button {
  animation: shadow 2s linear infinite;
}
.about__portrait:hover {
  border-width: 0;
}

.about__cards {
  margin: 3rem 0 1.5rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
}

.card.about__card {
  text-align: center;
}

.about__card-icon {
  font-size: 1.3rem;
  color: var(--color-black);
  border-radius: var(--border-radius-3);
  width: fit-content;
  margin-inline: auto;
  padding: 0.6rem;
  display: flex;
  align-items: center;
  transition: var(--transition);
}

.card.about__card:hover .about__card-icon {
  background: var(--color-primary);
  color: var(--color-white);
}
.card.about__card h5 {
  margin: 1rem 0;
}

.about__right p {
  margin-bottom: 0.8rem;
}

.about__right .btn {
  margin-top: 2.5rem;
}

/*MEDIA QUERIES FOR (TABLETTE)*/

@media screen and (max-width: 1024px) {
  .about__container {
    display: block;
  }
 
  .about__left {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-bottom: 1.5rem;
  }

  .about__right h2 {
    text-align: center;
  }
}
/*MEDIA QUERIES FOR (phones)*/
@media screen and (max-width: 600px) {
  .about__cards {
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
  }
  .about__portrait{
    width: 22rem;
    height: 22rem;
  }

  .video_cover video {
    width: 100%;
    height: 100%;
 
  }

  .about__right .btn {
    margin-top: 2rem;
  }
}
