footer{
    background-color: var(--color-light);
}
.footer__container{
    padding: 2.5rem 0;
    display: flex;
    justify-content: center;
    text-align: center;
    flex-direction: column;
    
}

.author__name{
    font-size: 1rem;
    font-weight: 600;
}

.social__media{
    justify-content: center;
    text-align: center;
    margin-top: 0.8rem;
    position: relative;
    left: 0;
    bottom: 0;
    display: flex;
    flex-direction: row;
    gap: 1rem;

}
.social__media a{
    width: 1.8rem;
   aspect-ratio: 1/1;
   background: var(--color-white);
   color: var(--color-black);
   display: grid;
   place-items: center;
   border-radius: var(--border-radius-3);
}
.social__media a:hover{
    background: var(--color-primary);
    color: var(--color-white);
    transform: translateY(-0.5rem);

}

.menu__field{
    width: 100%;
    height: 6rem;
    z-index: 999;
    display: none;
}

@media screen and (max-width:1024px){

    .menu__field{
        display: block;
    }
    
    
}
/*MEDIA QUERIES FOR (phones)*/
@media screen and (max-width:600px){
    .footer__container{
       
        display: flex;
        justify-content: space-around;
    }
    .menu__field{
        display: block;
    }
}