nav {
  width: 100vw;
  height: 5rem;
  display: grid;
  place-items: center;
  background: var(--color-primary);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;
  box-shadow: 0 1rem 1.5rem hsl(--primary-hue, 68%, 42%, 20%);
  transition: var(--transition);
}



.nav__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav__logo {
  width: 2.5rem;
  overflow: hidden;
}
.nav__menu {
  display: flex;
  gap: 3.5rem;
}

#theme__icon {
  background: transparent;
  color: white;
  font-size: 2rem;
  cursor: pointer;
}

.themes {
  position: absolute;
  width: 200px;
  top: 2.8rem;
  height: 8rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0.1rem;
  justify-content: center;
  border-radius: 1rem;
  background-color: var(--color-light);
  box-shadow: 1px 1px 5px #8f8f8f;
  padding: 1rem;
  transform: translateX(-43%);
  opacity: 0;
  transform: translateY(-100%);
  transition: opacity 0.5s, transform 0.5s;
}

.themes::before {
  content: "";
  display: inline-block;
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 20px solid var(--color-light);
  position: absolute;
  top: -10px;
  left: 45%;
}

.themes.visible {
  opacity: 1;
  transform: translateY(0%);
}

.themes div {
  width: 2rem;
  height: 2rem;
  z-index: 9;
  margin: auto;
  border-radius: 0.2rem;
  cursor: pointer;
}

#theme-white {
  background-color: rgb(255, 255, 255);
  box-shadow: 1px 1px 4px #9c9c9c;
}
#theme-blue {
  background-color: blue;
}
#theme-orange {
  background-color: orange;
}
#theme-purple {
  background-color: purple;
}
#theme-green {
  background-color: green;
}
#theme-black {
  background-color: black;
}

/* HIDE NAV MENU ON TABLETTE & PHONES*/
@media screen and (max-width: 1024px) {
  .nav__menu {
    display: none;
  }
  .themes {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    height: auto;
    width: auto;
  }
}

/* HIDE NAV MENU ON TABLETTE & PHONES*/
@media screen and (max-width: 600px) {
  .nav__menu {
    display: none;
  }
}
