
.work__filters{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-bottom: 2.5rem;
    margin-top: 2.5rem;
   
   
}

.work__filters .filter__pager
{
    display: flex;
    justify-content: space-between;
    width: 500px;
    padding: 0.5rem;
    flex-direction: row;
    height: auto;
    background-color: #c7c7c754;
    border-radius: var(--border-radius-3);
}



.work__item{
    width: 100%;
    /* background-color: var(--color-white); */
    /* box-shadow: 1px 1px 5px #33333354; */
    padding:  0.8rem;
    margin-right: 0.5rem;
    border-radius: var(--border-radius-3);
    cursor: pointer;
    font-weight: bold;
    transition: var(--transition);

}

.work__item:last-child
{
    margin-right: 0;
}



.work__item.active{
    background-color: var(--color-primary);
    color: var(--color-white);
}

/* .work__item:hover{
    color: var(--color-white);
    background-color: var(--color-primary);
} */

.work__container{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 1.875rem;
}

.work__card{
    position: relative;
    overflow: hidden;
    border-radius: var(--border-radius-1);
    padding: 1rem;
    opacity: 0;
    transform: scale(0);
    box-shadow: 1px 1px 10px 1px #3333335b;
    animation: popup 0.5s ease-in-out forwards;
    
}
@keyframes popup {
    from {
      opacity: 0;
      transform: scale(0);
    }
    to {
      opacity: 1;
      transform: scale(1);
    }
  }

.work__mask{
    background: var(--color-primary);
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    transition: var(--transition);
    opacity: 0;

}

.work__category{
    color: var(--color-white);
    background-color: var(--color-dark);
    border-bottom-left-radius: 0.98rem;
    border-bottom-right-radius: 0.98rem;
    position: absolute;
    top: 0;
    left: 1.5rem;
    font-size: 1rem;
    display: inline-block;
    padding: 0.19rem 0.625rem;
    transform: translateY(-40px);
    transition: var(--transition);
}

.work__title{
    color: var(--color-white);
    font-size: 1rem;
    margin: 0 0 0.98rem;
    padding: 0 1.25rem;
    position: absolute;
    opacity: 0;
    top: 3.75rem;
    transform: translateY(30px);
    transition: var(--transition);

}

.work__button{
    color: var(--color-white);
    position: absolute;
    bottom: 1.5rem;
    left: 1.5rem;
    font-size: 1.5rem;
    font-weight: 600;
    display: block;
    background-color: var(--color-primary);
    height: 40px;
    width: 40px;
    cursor: pointer;
    border-radius: 50%;
    text-align: center;
    line-height: 42px;
    transition: var(--transition);
    opacity: 0;

}

.work__card:hover .work__button{
    opacity: 1;
}

.work__card:hover .work__title,
.work__card:hover .work__category{
    opacity: 1;
    transform: translateY(0);
}

.work__card:hover .work__mask{
    opacity: 0.8;
}

/*MEDIA QUERIES (tablette)*/

@media screen and (max-width:1024px){

    .work__container{
        grid-template-columns: 1fr 1fr;
    }
}
/*MEDIA QUERIES (phones)*/
@media screen and (max-width:600px){
    .work__filters{
        width: 100%;
        column-gap: 0.9rem;
    }
    .work__container{
        grid-template-columns: 1fr;
    }

    .portfoli__container{
        padding: 0 10px;
        width: 100%;
    }

}