#skills{
    margin: 0;
    padding: 3rem 8rem;
}
.skills_section.grid{
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 4rem;
}

.skills_section .card h4{
    text-align: center;
    margin-bottom: 1rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid var(--color-light);
}

.skill{
    display: flex;
    padding: 0.555rem;
    height:auto;
    background-color: var(--color-light);
    border-radius: var(--border-radius-3);
    margin-bottom: 0.875rem;
}

.skill .img{
    width: 3.5rem;
    height: auto;
    padding: 0.5rem;
    margin-right: 0.875rem;
  
}

.skill .img img{
    width:2.5rem;
    height: 2.5rem;
    object-fit: contain;
}
.skill .right{
    width: 80%;
}

.skill .right .right-top{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.skill .right .seek-bar{
    width: 100%;
    height: 0.8rem;
    background-color: var(--color-white);
    margin-top: 0.60rem;
    border-radius: var(--border-radius-2);
}

.skill .right .seek-bar .seek-bar-fill{
    height: 100%;
    width: 0 ;
    background-color: var(--color-primary);
    border-radius: var(--border-radius-2);
    transition: width 1.5s ease-in-out;
}

.skill .right .seek-bar .seek-bar-fill.change-width{
    width: 100%;
}

.skill .right .seek-bar .seek-bar-fill.html{
    width: 90% ;
}
.skill .right .seek-bar .seek-bar-fill.css{
    width: 80% ;
}
.skill .right .seek-bar .seek-bar-fill.bootstrap{
    width: 80% ;
}
.skill .right .seek-bar .seek-bar-fill.tailwind{
    width: 50% ;
}
.skill .right .seek-bar .seek-bar-fill.js{
    width: 75% ;
}
.skill .right .seek-bar .seek-bar-fill.react_js{
    width: 65% ;
}

.skill .right .seek-bar .seek-bar-fill.angnular{
    width: 50% ;
}
.skill .right .seek-bar .seek-bar-fill.spring{
    width: 60% ;
}
.skill .right .seek-bar .seek-bar-fill.php{
    width: 75% ;
}
.skill .right .seek-bar .seek-bar-fill.laravel{
    width: 70% ;
}
.skill .right .seek-bar .seek-bar-fill.msql{
    width: 70% ;
}
.skill .right .seek-bar .seek-bar-fill.java{
    width: 70%;
}
.skill .right .seek-bar .seek-bar-fill.sqlite{
    width: 75%;
}
.skill .right .seek-bar .seek-bar-fill.C{
    width: 70%;
}
.skill .right .seek-bar .seek-bar-fill.Cpp{
    width: 60%;
}

/*MEDIA QUERIES (tablette)*/

@media screen and (max-width:1024px){

    #skills{
        margin: 0;
        padding: 3rem 0.785rem;
    }
    .skills_section.grid{
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 1.2rem;
    }
}
/*MEDIA QUERIES (phones)*/
@media screen and (max-width:600px){
    #skills{
        margin: 0;
        padding: 3rem 0.785rem;
    }
    .skills_section.grid{
        display: grid;
        grid-template-columns: 1fr ;
        column-gap: 1.2rem;
    }
    .skills_section .card:last-child{
        margin-top: 1rem;
    }

}