#floting__nav{
    margin: 0;
    padding: 0;
    background-color: transparent;
    text-align: center;
    align-content: center;
    position: fixed;
    position: fixed;
    bottom: 0.5rem;
    left: 0;
    width: 100%;
    z-index: 100;
}

.floting_nav_items{
    display: none;
    justify-content: space-around;
    gap: 0.5rem;
    padding: 1rem 2rem;
    width: 40%;
    margin: auto;
    background-color: transparent;
    border-radius: var(--border-radius-1);
    box-shadow: 2px 2px 5px #33333373;
}
.floting_nav_items::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: var(--border-radius-1);
    backdrop-filter: blur(6px); 
      transition: var(--transition);
      z-index: -1;
    
      
 }

.floting_nav_items .nav_icon{
    width: 2.6rem;
    height: 2.6rem;
    font-size: 1.6rem;
    cursor: pointer;
    padding: 0.4rem;
    border-radius: 50%;
    align-items: center;
    color: var(--color-primary);
    transition: var(--transition);
}

.floting_nav_items .nav_icon.active{
    background-color: var(--color-primary);
    color: var(--color-white);
}

/* HIDE NAV MENU ON TABLETTE & PHONES*/
@media screen and (max-width: 1024px){
    .floting_nav_items{
        display: flex;
        width: 70%;  
        transform: translateX(0);
        margin:auto;    
        
    }
}

/* HIDE NAV MENU ON TABLETTE & PHONES*/
@media screen and (max-width: 600px){
    .floting_nav_items{
        width: 95%;
        margin: auto;  
        transform: translatex(0);    
        
    }
}
